import { resetCart } from '../api';

/** Actions */
export const handleFetchError = (
  errors: Array<{ message: string; extensions: { category: string } }>
) => {
  const unauthorized =
    errors.findIndex(
      ({ extensions }) => extensions?.category === 'graphql-authorization'
    ) > -1;

  const missingCart =
    errors.findIndex(
      ({ extensions }) => extensions?.category === 'graphql-no-such-entity'
    ) > -1;

  const errorMessage = errors.map((e: any) => e.message).join(' ');

  // Reset cart data if unauthorized or missing cart
  if (unauthorized || missingCart) {
    resetCart();
    console.error(errorMessage);
    return null;
  }

  throw Error(errorMessage);
};
