import {
  CART_FRAGMENT,
  CART_ITEMS_PAGINATION_ARGUMENTS,
} from '@/cart/api/graphql/CartFragment';

export const GUEST_CART_QUERY = `
  query GUEST_CART_QUERY(
      $cartId: String!,
      ${CART_ITEMS_PAGINATION_ARGUMENTS}
    ) {

    cart(cart_id: $cartId){
      ...CartFragment
    }
  }

  ${CART_FRAGMENT}
`;

export const CUSTOMER_CART_QUERY = `
  query CUSTOMER_CART_QUERY(
      ${CART_ITEMS_PAGINATION_ARGUMENTS}
    ) {

    cart: customerCart {
      ...CartFragment
    }
  }

  ${CART_FRAGMENT}
`;
