import { fetchGraphQl } from '@/cart/api';
import { StoreConfigModel } from '@/cart/data/models';
import { transformStoreConfig } from '@/cart/data/transforms';
import { handleFetchError } from '@/cart/lib/fetch-error';

import { STORE_CONFIG_QUERY } from './graphql/StoreConfigQuery';

export const getStoreConfig = async (): Promise<StoreConfigModel | null> => {
  return fetchGraphQl(STORE_CONFIG_QUERY, {
    method: 'GET',
    cache: 'force-cache',
  }).then(({ errors, data }) => {
    if (errors) return handleFetchError(errors);

    return transformStoreConfig(data.storeConfig);
  });
};
